// @ts-ignore
import utils from 'mageUtils';
import Component from 'uiComponent';

export default Component.extend({
    defaults: {
        ignoreTmpls: {
            data: true
        }
    },
    setData(oldData: any, newData: any, current: any, parentPath: string) {
        Object.entries(oldData)
            .forEach(([key, val]) => {
                switch (true) {
                    case val instanceof Array:
                    case val instanceof Object:
                        this.setData(oldData[key], val, current[key], utils.fullPath(parentPath, key));
                        break;
                    case val != oldData[key] && oldData[key] == current[key]:
                        this.set(utils.fullPath(parentPath, key), val);
                        break;
                    default:
                        break;
                }
            })
    }
})
